@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&family=Sen:wght@400;700&display=swap');

/* VARIABLES */
:root {
    --bg-prim: rgb(225, 225, 225);
    --bg-secn: rgb(238, 238, 238);
    --bg-tert: rgb(240, 240, 240);
    --acc-prim: rgb(16, 16, 16);
    --acc-secn: rgba(0, 0, 0, 0.355);
    --trailer: black;
    --trailer-color: white;
    --svg-color: rgb(231, 231, 231);
}

[data-theme="dark"] {
  --bg-prim: rgb(19, 19, 19);
  --bg-secn: rgb(43, 43, 43);
  --bg-tert: rgb(73, 73, 73);
  --acc-prim: rgb(237, 237, 237);
  --acc-secn: rgba(237, 237, 237, 0.355);
  --trailer: white;
  --trailer-color: black;
  --svg-color: rgb(50, 50, 50);
}

* {
  transition: color 200ms linear, background-color 200ms linear;
}

/* SCROLLBARS */
  /* Firefox */
  * {
    scrollbar-width: thin;
    scrollbar-color: var(--bg-prim) var(--acc-prim);
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 12px;
    height: 0px;
  }

  *::-webkit-scrollbar-track {
    background:var(--bg-prim);
  }

  *::-webkit-scrollbar-thumb {
    background-color: var(--acc-prim);
    border-radius: 10px;
    border: 3px solid var(--bg-prim);
  }

/* CSS RESET */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: 1.05em;
}  

/* MAIN CSS */

body {
    margin: 0;
    padding: 10;

    font-family: Sen, sans-serif;
    line-height: 1.4;
    font-size: 0.95em;
    color: var(--acc-prim);
    background-color: var(--bg-prim);   
}

h1 {
    font-size: 1.9em;
}

section {
    text-align: center;
    scroll-snap-align: start;
    scroll-snap-type: mandatory;
}

a {
  text-decoration: none;
}

svg {
  // height: 1.5em;
  fill: var(--svg-color);
}
